body {
  font-family: "Work Sans", monospace, Helvetica, sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
  margin: 0;
  margin-top: 12px;
}

.grad {
  width: 100%;
  max-height: 33vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.grad > img {
  max-height: 100%;
}

.text {
  width: 100%;
  text-align: right;
  font-size: 2em;
  padding: 36px;
  margin-bottom: 24px;
  max-width: 750px;
}

a,
a:visited {
  color: black;
}
a:hover {
  background-color: yellow;
}
